import React, { useState } from "react";
import Layout from "../../../layout/AppDefaultLayout";
import MuiTooltip from "../../../components/MuiTooltip";
import TextToggle from "../../../components/TextToggle";
import Input from "../../../components/Input";
import Button from "../../../components/Button";
import Checkbox from "../../../components/Checkbox";
import OrderListCard from "./OrderListCard";
import CouponListModal from "./CouponListModal";
import PayAlert from "./PayAlert";


const OrderListData = [
  {
    id: 1,
    image: require("../../../images/item.png"),
    product: "",
    price: 3800,
    hoticed: "iced",
    density: {
      opt: "",
      price: 0,
    },
    amount: 2,
    discount: [
      {
        title: " ",
        percent: 100,
        count: 1,
      },
      {
        title: " ",
        percent: 73.95,
        count: 1,
      },
    ],
  },
  {
    id: 2,
    image: require("../../../images/item01.png"),
    product: "",
    price: 5200,
    hoticed: "hot",
    option: "",
    amount: 1,
    density: {
      opt: "",
      price: 500,
    },
    discount: [
      {
        title: " ",
        percent: 100,
        count: 1,
      },
    ],
  },
  {
    id: 3,
    image: require("../../../images/item01.png"),
    product: "",
    price: 4700,
    hoticed: "iced",
    density: {
      opt: "",
      price: 0,
    },
    amount: 2,
    discount: [
      {
        title: " ",
        percent: 73.95,
        count: 2,
      },
    ],
  },
];
const CouponListData = [
  {
    id: 1,
    image: require("../../../images/item.png"),
    issuer: "MEGAZONE",
    title: "    1",
    date: "2023.10.31",
  },
  {
    id: 2,
    image: require("../../../images/item.png"),
    issuer: "MEGAZONE",
    title: "    1",
    date: "2023.10.31",
  },
  {
    id: 3,
    image: require("../../../images/item.png"),
    issuer: "MEGAZONE",
    title: "    1",
    date: "2023.10.31"
  },
]

function PaymentPage() {
  
  const [menuOn, setMenuOn] = useState(true);
  const menuToggle = () => {
    setMenuOn(!menuOn);
  }
  const [discountOn, setDiscountOn] = useState(true);
  const discountToggle = () => {
    setDiscountOn(!discountOn);
  }

  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const [AlertOpen, setalertOpen] = useState(false);
  const AlerthandleOpen = () => {
    setalertOpen(true);
  };
  const AlerthandleClose = () => {
    setalertOpen(false);
  };

  const pricesum = OrderListData.reduce(
    (total, currentvalue) =>
      (total = total + parseInt(currentvalue.price * currentvalue.amount)),
    0
  );
  return (
    <Layout Back Title="">
      <div className="payment">
        <div className="inner">
          <p className="headline4 mb_20"></p>
          <p className="bold500 body0"> (1)</p>
        </div>
        <hr />
        <div className="inner">
          <div className="align">
            <p className="headline4"></p>
            <button
              className={`togglebtn ${menuOn ? "open" : ""}`}
              onClick={menuToggle}
            >
              <i className="arrow"></i>
            </button>
          </div>
          {menuOn ? <OrderListCard data={OrderListData} /> : ""}
        </div>
        <hr />
        <div className="inner">
          <div className="align start mb_20">
            <p className="headline4 mr_4">   </p>
            <MuiTooltip
              iconSize={{ width: 16, height: 16, fill: "var(--c99)" }}
              placements="bottom-start"
              arrow
            >
                 
            </MuiTooltip>
          </div>
          <TextToggle
            data={[
              {
                defaultActive: true,
                id: 1,
                option: null,
                title: "",
              },
              {
                id: 2,
                option: null,
                title: "",
              },
              {
                id: 3,
                option: null,
                title: "",
              },
            ]}
            type="linebox"
          />
        </div>
        <hr />
        <div className="inner">
          <p className="headline4 mb_20"></p>
          <div className="align coupon gap_10">
            <Input
              height="large"
              defaultValue=""
              placeholder="  ."
              readonly
            />
            <Button line size="small" onClick={handleOpen}>
              
            </Button>
          </div>
        </div>
        <hr />
        <div className="inner payment_info">
          <p className="headline4 mb_20"></p>
          <ul className="payment_info_list">
            <li className="align mb_20">
              <p></p>
              <p>
                <span>{pricesum.toLocaleString()}</span>
              </p>
            </li>
            <li>
              <div className="align mb_14">
                <p className="align start red_text">
                  
                  <button
                    className={`togglebtn ml_4 ${discountOn ? "open" : ""}`}
                    onClick={discountToggle}
                  >
                    <i className="arrow red"></i>
                  </button>
                </p>
                <p className="align end red_text">
                  <span>- 17,210</span> 
                </p>
              </div>
              {discountOn ? (
                <ul className="discount">
                  <li>
                    <p className="red_text"></p>
                    <p className="red_text">
                      <span>9,000</span>
                    </p>
                  </li>
                  <li className="detail">
                    <p>[]  1</p>
                    <p>
                      <span>3,800</span> 
                    </p>
                  </li>
                  <li className="detail">
                    <p>[]  1</p>
                    <p>
                      <span>5,200</span> 
                    </p>
                  </li>
                  <li>
                    <p className="red_text"></p>
                    <p className="red_text">
                      <span>8,210</span> 
                    </p>
                  </li>
                </ul>
              ) : (
                ""
              )}
            </li>
          </ul>
          <div className="payment_info_total align">
            <p className="headline3"> </p>
            <p className="red_text headline1">
              <span className="pr_2">4,990</span>
            </p>
          </div>
        </div>
        <hr />
        <div className="inner">
          <p className="headline4 mb_20"></p>
          <ul className="bullet_list gray">
            <li> .</li>
            <li> .</li>
            <li> .</li>
          </ul>
        </div>
        <hr />
        <div className="inner">
          <Checkbox txt={"   "} />
          <Button
            size="full"
            globalClass="mt_20"
            onClick={AlerthandleOpen}
          >
            <p className="align gap_4">
              <span></span>
              <span>
                4,990<em></em>
              </span>
              <span></span>
            </p>
          </Button>
        </div>
      </div>
      <CouponListModal
        open={open}
        handleClose={handleClose}
        data={CouponListData}
      />
      <PayAlert open={AlertOpen} handleClose={AlerthandleClose} />
    </Layout>
  );
}

export default PaymentPage;