import React, { useState } from "react";
import Layout from '../../layout/AppDefaultLayout';
import MainimgDefult from "../../images/main_img.gif";

import FullMenu from "./FullMenuPage";
import FavoritesMenu from "./FavoritesMenuPage";

import Tabs from "../../components/Tabs";

import { Icons } from "../../components/Icon";

const LocationsData = [
  {
    id: 1,
    title: " ",
    location: "   85 46  1F",
    time: " 08:00~22:00",
    state: "",
  },
  {
    id: 2,
    title: "9 ",
    location: "  17 57 1F",
    time: " 08:00~22:00",
    state: "",
  },
  {
    id: 3,
    title: " ",
    location: "   471-1",
    time: " 08:00~22:00",
    state: "",
  },
];

function Locations() {
  const [open, setOpen] = useState(false);
  const [titleSelect, setTitleSelect] = useState(
    <p className="headline1">
      <span></span>
      <span> !</span>
    </p>
  );

  const LocationonCLick = () => {
    setOpen(!open);
  };
  const ListCLick = (location) => {
    setOpen(!open);
    setTitleSelect(
      <p>
        <span className="headline1">{location.title}</span>
        <span className="mt_12">
          {location.state === "" && <em></em>}
          {location.state === "" && (
            <em className="red_bg"></em>
          )}
          {location.time}
        </span>
      </p>
    );
  };
  return (
    <div className="main_locations">
      <button onClick={LocationonCLick}>
        {titleSelect}
        <i className={`${open ? "open" : ""}`}>
          <Icons.ArrowBg
            width={36}
            height={36}
            fill="var(--blue)"
            stroke="#DAEE5F"
          />
        </i>
      </button>
      <div className={`${"main_locations_list"} ${open ? "open" : ""}`}>
        <ul>
          {LocationsData.map((location) => (
            <li key={location.id}>
              <button onClick={() => ListCLick(location)}>
                <i>
                  <Icons.LogoOnly width={20} height={20} />
                </i>
                <p className="optionItem">
                  <span>{location.title}</span>
                  <span>{location.location}</span>
                  <span>{location.time}</span>
                </p>
              </button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  ); 
}

function QRMainPage() {
  return (
    <Layout Coupon Cart Menu>
      <div className="main">
        <div className="align top main_visual">
          <div className="lft">
            <p>
              <span className="headline4 mb_16">Good morning</span>
              <span>
                <strong> </strong>
              </span>
              <span>
                <em className="neon"></em>
              </span>
              <span>
                <em className="neon"> ?</em>
              </span>
            </p>
          </div>
          <div className="rgt">
            <img src={MainimgDefult} alt=" " />
          </div>
        </div>
        <div className="main_content">
          <Locations />
          <FullMenu QR />
        </div>
      </div>
    </Layout>
  );
}

export default QRMainPage;
