import React from "react";
import MuiModal from "../../../components/MuiModal";
import Button from "../../../components/Button";

function OrderDetailModal({ open, handleClose }){
  return (
    <MuiModal
      open={open}
      onClose={handleClose}
      header={
        <>
          <h4> </h4>
          <Button
            icon="Delete"
            none
            onClick={handleClose}
            size="icon_s"
            iconStyle={{
              fill: "var(--primary)",
            }}
          >
            Close
          </Button>
        </>
      }
    >      
      <div className="admin_order">
        <div className="admin_order_title">
          <p> / </p>
          <p></p>
        </div>
        <ul>
          <li>
            <div className="order_list_product">
              <p>
                <strong className="body2"></strong>
                <span className="mt_5 body2">
                  <em>ICED</em> /
                  <em></em>
                </span>
              </p>
              <p className="body2">
                7,600
              </p>
            </div>
            <ul className="order_list_discount">
              <li>
                <p className="body2">
                  <span>[]</span>
                  <span>  1</span>
                </p>
                <p className="body2">- 3,800</p>
              </li>
              <li>
                <p className="body2">
                  <span>[]</span>
                  <span> </span>
                </p>
                <p className="body2">- 2,810</p>
              </li>
            </ul>
          </li>
          <li>
            <div className="order_list_product">
              <p>
                <strong className="body2"></strong>
                <span className="mt_5 body2">
                  <em>HOT</em> /
                  <em></em>
                </span>
              </p>
              <p className="body2">4,100</p>
            </div>
            <ul className="admin_order_discount">
              <li>
                <p className="body2">
                  <span>[]</span>
                  <span> </span>
                </p>
                <p className="body2">- 2,810</p>
              </li>              
            </ul>
          </li>
          <li>
            <div className="admin_order_sum">
              <p><strong className="body2"></strong></p>
              <p className="body2">21,100</p>
            </div>
            <ul className="admin_order_sum_discount">
              <li>
                <p><strong className="body2 red"> </strong></p>
                <p className="body2 red">-16,820</p>
              </li>
            </ul>
          </li>
          <li>
            <div className="admin_order_sum mt_5">
              <p><strong className="headline5"></strong></p>
              <p className="headline5">4,280</p>
            </div>
          </li>
        </ul>
      </div>
      
      
    </MuiModal>
  );
}

export default OrderDetailModal;