import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Layout from "../../../layout/AppLoginLayout";
import MainimgDefult from "../../../images/main_img.gif";

import Input from "../../../components/Input";
import Button from "../../../components/Button";
import FormGroup from "../../../components/FormGroup";
import Checkbox from "../../../components/Checkbox";

// import { Icons } from "../../components/Icon";

function LoginPage() {
  const navigate = useNavigate();
  return (
    <Layout>
      <div className="login">
        <div className="align top login_visual">
          <div className="lft">
            <p>
              <span className="headline4 mb_16">Weve already met!</span>
              <span className="headline0">
                <strong>
                   <em className="bold400">:)</em>
                </strong>
              </span>
              <span>
                <em className="neon"></em>
              </span>
              <span>
                <em className="neon">.</em>
              </span>
            </p>
          </div>
          <div className="rgt pt_30">
            <img src={MainimgDefult} alt=" " />
          </div>
        </div>
        <div className="login_content">
          <div className="login_box">
            <div className="login_box_input align column ">
              <div>
                <Input
                  onClick={() => {}}
                  shape="none"
                  globalClass="login_input"
                  placeholder="ID"
                />
              </div>
              <div>
                <Input
                  onClick={() => {}}
                  shape="none"
                  globalClass="login_input"
                  type="password"
                  placeholder="Password"
                />
              </div>
            </div>
            <div className="login_btn mt_20">
              <div>
                <Button
                  btntype="blue"
                  onClick={() => {
                    navigate("/main");
                  }}
                  size="full"
                >
                  
                </Button>
              </div>
              <div className="mt_20 pt_20">
                <Button
                  btntype="googlelogin"
                  icon="Google"
                  onClick={() => {}}
                  size="full"
                >
                  Google  
                </Button>
              </div>
            </div>
            <div className="mt_20">
              <FormGroup>
                <Checkbox name="contact00" id="check1" value="Login" txt={"  "} />
              </FormGroup>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default LoginPage;
