import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Layout from "../../../layout/AppLoginLayout";
import Mainimg10 from "../../../images/main_img10.gif";
import Mainimg12 from "../../../images/main_img12.gif";
import MainimgDefult from "../../../images/main_img.gif";

import Input from "../../../components/Input";
import Button from "../../../components/Button";
import FormGroup from "../../../components/FormGroup";
import Checkbox from "../../../components/Checkbox";

// import { Icons } from "../../components/Icon";


function Dateimages() {
  const today = new Date();
  //   .

  const formattedDate = today.getMonth() + 1;
  //    .
  switch (formattedDate) {
    case 10:
      return <img src={Mainimg10} alt=" " />;
    case 12:
      return <img src={Mainimg12} alt=" " />;
    default:
      return <img src={MainimgDefult} alt=" " />;
  }
}

function QRLoginPage() {
  const navigate = useNavigate();
  const [certification, setCertification] = useState(false);
  const certifications = () => {
    setCertification(true);
  };
  return (
    <Layout>
      <div className="login">
        <div className="align top login_visual">
          <div className="lft">
            <p>
              <span className="headline4 mb_16">Weve already met!</span>
              <span className="headline0">
                <strong>
                   <em className="bold400">:)</em>
                </strong>
              </span>
              <span>
                <em className="neon"></em>
              </span>
              <span>
                <em className="neon">.</em>
              </span>
            </p>
          </div>
          <div className="rgt pt_30">
            <Dateimages />
          </div>
        </div>
        <div className="login_info">
          <p>
            <span>  </span>
            <span>  .</span>
          </p>
        </div>
        <div className="login_content">
          <div className="login_box">
            <div className="login_box_input align column ">
              <div>
                <Input
                  onClick={certifications}
                  shape="none"
                  globalClass="login_input"
                  placeholder="  "
                  phone
                  maxLength={13}
                />
              </div>
              <div>
                <Input
                  onClick={() => {}}
                  shape="none"
                  globalClass="login_input"
                  type="text"
                  placeholder=" "
                  timer={certification === true}
                  disabled={certification === false}
                />
              </div>
            </div>
            <div className="login_btn mt_20">
              <div>
                <Button
                  btntype="blue"
                  onClick={() => {
                    navigate("/qrmain");
                  }}
                  size="full"
                  disabled={certification === false}
                >
                  
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default QRLoginPage;
