import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Layout from '../../../layout/DefaultLayout'
import ContentBox from "../../../layout/ContentBox";
import Button from "../../../components/Button";
import Input from "../../../components/Input";
import Table from "../../../components/Table";
import FileUpload from "../../../components/FileUpload";
import Radio from "../../../components/Radio";
import FormGroup from "../../../components/FormGroup";
import Textarea from "../../../components/Textarea";

//mui table import
import MuiAlert from "../../../components/MuiAlert";

  function NoticeRegistrationPage() {
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Layout>
      <div className="align mb_20">
        <h1 className="headline2">  </h1>
      </div>
      <hr className="primary" />
      <ContentBox>
        <Table
          colgroup={
            <>
              <col width="15%" />
              <col width="auto" />
            </>
          }
        >
          <tr>
            <th className="required"></th>
            <td>
            <FormGroup>
              <Radio name="contact0" id="radio1" value="All" defaultChecked>
                
              </Radio>
              <Radio name="contact0" id="radio2" value="employees">
                
              </Radio>
              <Radio name="contact0" id="radio3" value="tenantcompany">
                
              </Radio>
              <Radio name="contact0" id="radio4" value="visitor">
                
              </Radio>
            </FormGroup>
            </td>
          </tr>
          <tr>
            <th className="required"></th>
            <td>
            <FormGroup>
              <Radio name="contact1" id="radio5" value="exposure" defaultChecked>
                
              </Radio>
              <Radio name="contact1" id="radio6" value="nonexposed">
                
              </Radio>
            </FormGroup>
            </td>
          </tr>
          <tr>
            <th className="required"></th>
            <td>
              <Input
                onClick={() => { }}
                placeholder="  ."
                shape="round"
              />
            </td>
          </tr>
          <tr>
            <th className="required"></th>
            <td>
              <div className="admin_editorarea mb_8">  .</div>
              <Textarea Height={200} />
            </td>
          </tr>
          <tr>
            <th> </th>
            <td>
              <FileUpload onChange={() => {}} />
            </td>
          </tr>
        </Table>
        <div className="align end mt_20 gap_8">
          <Button onClick={() => {}} size="xlarge" line>
            
          </Button>
          <Button onClick={() => { }} size="xlarge">
            
          </Button>
        </div>
      </ContentBox>
      <MuiAlert
          open={open}
          onClose={handleClose}
          title={
            <>
                <br/>  ?
            </>
          }
          button={
            <>
              <Button onClick={handleClose} line></Button>
              <Button onClick={() => { }}></Button>
            </>
          }
        >
        </MuiAlert>
    </Layout>
  );
}

export default NoticeRegistrationPage;
