import React from "react";
import MuiModal from "../../../components/MuiModal";
import Button from "../../../components/Button";
import TextToggle from "../../../components/TextToggle";


function ChangeOptionModal({ open, handleClose }){
  return(
    <>
      <MuiModal
        open={open}
        onClose={handleClose}
        header={
          <>
            <h4> </h4>
            <Button
              icon="Delete"
              none
              onClick={handleClose}
              size="icon_s"
              iconStyle={{
                fill: "var(--primary)",
              }}
            >
              Close
            </Button>
          </>
        }
        button={
          <>
            <Button onClick={handleClose} line> </Button>
            <Button onClick={handleClose} globalClass="ml_10">
               
            </Button>
          </>
        }
      >
        <div className="modal_line">
          <p className="headline2"></p>
        </div>
        <div>
          <p className="headline5 bold400 pb_17">HOT/ICE </p>
          <TextToggle
            data={[
              {
                id: 1,
                option: null,
                title: "HOT",
              },
              {
                id: 2,
                option: null,
                title: "ICED",
                defaultActive: true,
              },
            ]}
          />
        </div>
        <div className="mt_20">
          <p className="headline5 bold400 pb_17"> </p>
          <TextToggle
            data={[
              {
                id: 1,
                option: null,
                title: " (2)",
                defaultActive: true,
              },
              {
                id: 2,
                option: null,
                title: " (1)",
              },
              {
                id: 3,
                option: true,
                title: " (1)",
              },
            ]}
          />
        </div>
      </MuiModal>
    </>
  );
}

export default ChangeOptionModal;