import React, {useState} from "react";
import Layout from "../../../layout/AppDefaultLayout";

const qnaList = [
  {
    category: "category1",
    question: "    ?",
    answer: "    .",
  },
  {
    category: "category2",
    question: "    ? 2",
    answer: "     2",
  },
  {
    category: "category3",
    question: "    ? 3",
    answer: "     3",
  },
  {
    category: "category4",
    question: "    ? 4",
    answer: "     4",
  },
  {
    category: "category5",
    question: "    ? 5",
    answer: "     5",
  },
  {
    category: "category6",
    question: "    ? 6",
    answer: "     6",
  },
];

function Faq() {
  const [activeIndex, setActiveIndex] = useState(null);

  return (
    <Layout Back Menu Title=" ">
      <div className="inner">
        {qnaList.map((item, index) => (
          <div className="faq-list" key={index}>
            <div 
              className={`faq-item align ${activeIndex === index ? 'active' : ''}`}
              onClick={() => setActiveIndex(activeIndex === index ? null : index)}
              >
              <div className="question">
                {item.question}
              </div>
              <i className={`arrow ${activeIndex === index ? "up" : ""}`}></i>
            </div>
            {activeIndex === index && <div className="answer">{item.answer}</div>}
          </div>
        ))}
      </div>
    </Layout>
  );
}

export default Faq;

