import React from "react";
import Layout from '../../../layout/DefaultLayout'
import ContentBox from "../../../layout/ContentBox";
import Table from "../../../components/Table";
import Input from "../../../components/Input";
import Select from "../../../components/Select";
import Button from "../../../components/Button";

function AccountModify() {
  
  return (
    <Layout>
      <div className="align mb_20">
        <h1 className="headline2"> </h1>
      </div>
      <ContentBox>
        <Table colgroup={<><col width="20%" /><col /></>}>
          <tr>
            <th className="required"></th>
            <td>
              <p className="body0 color_t_99"> </p>
            </td>
          </tr>
          <tr>
            <th className="required"></th>
            <td>
              <p className="body0 color_t_99"></p>
            </td>
          </tr>
          <tr>
            <th className="required">ID</th>
            <td>
              <p className="body0 color_t_99">admin01</p>
            </td>
          </tr>
          <tr>
            <th className="required"></th>
            <td>
              <Input
                onClick={() => { }}
                type="password"
                placeholder="  ."
                defaultValue="1234567890"
                disabled
              />
            </td>
          </tr>          
          <tr>
            <th className="required"> </th>
            <td>
              <Select
                placeholder=""
                defaultValue={0}
                options={[
                  {
                    label: ' ',
                    value: ' '
                  },
                  {
                    label: ' ',
                    value: ' '
                  }
                  ,
                  {
                    label: ' ',
                    value: ' '
                  }
                ]}
              />
            </td>
          </tr>
        </Table>
        <div className="align mt_20">
          <div className="rgt gap_10">
            <Button
              onClick={() => { }}
              round
              size="medium"      
              line  
            >
               
            </Button>
            <Button
              onClick={() => { }}
              round
              size="medium"
              btntype="gray"
            >
              
            </Button>
            <Button
              onClick={() => { }}
              round
              size="medium"
            >
              
            </Button>
          </div>
        </div>
      </ContentBox>
    </Layout>
  );
}

export default AccountModify;
