import React, { useState, useCallback } from "react";
import { Swiper, SwiperSlide, useSwiper } from "swiper/react";
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Navigation, Pagination } from "swiper/modules";
import Layout from '../../../layout/AppDefaultLayout'
// import { Icons } from "../../../components/Icon";
// import { Button } from "../../../components/Button"
import { Tabs } from "../../../components/Tabs";
import CouponSwiper from "./CouponSwiper";
// import { Checkbox } from "../../../components/Checkbox"
// import { Count } from "../../../components/Count"

// import Menuitem from "../../../images/item.png";

const CouponData = [
  {
    id: 1,
    type: "Americano",
    title: "",
    issuer: ".",
    date: "",
    image: require("../../../images/couponitem.png"),
    use: false,
    usename: "",
  },
  {
    id: 2,
    type: "Americano",
    title: "",
    issuer: ".",
    date: "",
    image: require("../../../images/couponitem.png"),
    use: true,
    usename: "",
  },
  {
    id: 3,
    type: "Drink",
    title: "",
    issuer: ".",
    date: "",
    image: require("../../../images/couponitem.png"),
    use: true,
    usename: "",
  },
  {
    id: 4,
    type: "Americano",
    title: "",
    issuer: ".",
    date: "",
    image: require("../../../images/couponitem.png"),
    use: false,
    usename: "",
  },
  {
    id: 5,
    type: "Drink",
    title: "",
    issuer: ".",
    date: "",
    image: require("../../../images/couponitem.png"),
    use: false,
    usename: "",
  },
];

const UnUse = CouponData.filter(
  (CouponData) => CouponData.use === false
);

const Use = CouponData.filter(
  (CouponData) => CouponData.use === true
);

function CouponPage() {
  return (
    <Layout Menu Back Title="">
      <hr />
      <Tabs
        TabsData={[
          {
            id: 1,
            title: "  ",
            defaultActive: true,
            content: (
              <>
                <CouponSwiper data={UnUse} />
              </>
            ),
          },
          {
            id: 2,
            title: " ",
            content: (
              <>
                <CouponSwiper data={Use} />
              </>
            ),
          },
        ]}
        globalClass="coupon_tab"
      />
    </Layout>
  );
}
export default CouponPage;
