import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Layout from '../../../layout/DefaultLayout'
import ContentBox from "../../../layout/ContentBox";
import Button from "../../../components/Button";
import Input from "../../../components/Input";
import Textarea from "../../../components/Textarea";
import Table from "../../../components/Table";
import Select from "../../../components/Select";
import MuiModal from "../../../components/MuiModal";
import FormGroup from "../../../components/FormGroup";
import Radio from "../../../components/Radio";
import MuiPage from "../../../components/MuiPage";

//mui table import
import MuiTable from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';


const categoryOpt = [
  {
    label: "",
    value: "",
  },
  {
    label: "Coffee",
    value: "Coffee",
  },
  {
    label: "Non-Coffee",
    value: "Non-Coffee",
  },
  {
    label: "Ade/Juice",
    value: "Ade/Juice",
  },
  {
    label: "Blended",
    value: "Blended",
  },
  {
    label: "Tea",
    value: "Tea",
  },
  {
    label: "Bread",
    value: "Bread",
  },
  {
    label: "Bottle",
    value: "Bottle",
  },
]

const storeNameOpt = [
  {
    label: "",
    value: "",
  },
  {
    label: "(1)",
    value: "(1)",
  },
  {
    label: "9(2)",
    value: "9(2)",
  },
];

const numOpt = [
  {
    label: "20 ",
    value: "20 ",
  },
  {
    label: "30 ",
    value: "30 ",
  },
  {
    label: "50 ",
    value: "50 ",
  },
];


function ProductListPage() {
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <Layout>
      <div className="align mb_20">
        <h1 className="headline2"></h1>
      </div>
      <hr className="primary" />
      <ContentBox top>
        <Table
          colgroup={
            <>
              <col width="15%" />
              <col width="auto" />
              <col width="15%" />
              <col width="auto" />
            </>
          }
        >
          <tr>
            <th> </th>
            <td>
              <Select
                width="350px"
                round="round"
                defaultValue={0}
                options={categoryOpt}
              />
            </td>
            <th> </th>
            <td>
              <Select
                width="350px"
                round="round"
                defaultValue={0}
                options={storeNameOpt}
              />
            </td>
          </tr>
          <tr>
            <th>  (ID, )</th>
            <td colSpan={3}>
              <Input
                onClick={() => {}}
                placeholder="ID   ."
                shape="round"
              />
            </td>
          </tr>
        </Table>
        <div className="align center mt_20">
          <Button onClick={handleOpen} btntype="c11" size="xlarge">
            
          </Button>
        </div>
      </ContentBox>
      <hr className="secondary" />
      <div className="align mt_42">
        <div className="lft">
          <Select
            minwidth="200px"
            round="round"
            defaultValue={0}
            options={numOpt}
          />
        </div>
        <div className="rgt">
          <Button onClick={handleOpen} size="small_h35" line globalClass="mr_5">
            
          </Button>
          <Button onClick={handleOpen} size="small_h35">
            
          </Button>
        </div>
      </div>
      <ContentBox>
        <div className="tbl">
          <TableContainer>
            <MuiTable sx={{ minWidth: 650 }} aria-label="simple table">
              <colgroup>
                <col width="10%" />
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
                <col width="20%" />
                <col width="10%" />
              </colgroup>
              <TableHead>
                <TableRow>
                  <TableCell>No.</TableCell>
                  <TableCell></TableCell>
                  <TableCell> ID</TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow>
                  <TableCell>1</TableCell>
                  <TableCell></TableCell>
                  <TableCell>admin01</TableCell>
                  <TableCell></TableCell>
                  <TableCell> </TableCell>
                  <TableCell>
                    <Link to="/admin/account/modify"> </Link>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>2</TableCell>
                  <TableCell></TableCell>
                  <TableCell>admin02</TableCell>
                  <TableCell>010-***-1234</TableCell>
                  <TableCell> </TableCell>
                  <TableCell>
                    <Link to="/admin/account/modify"> </Link>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>3</TableCell>
                  <TableCell></TableCell>
                  <TableCell>admin03</TableCell>
                  <TableCell>3</TableCell>
                  <TableCell> </TableCell>
                  <TableCell>
                    <Link to="/admin/account/modify"> </Link>
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>4</TableCell>
                  <TableCell></TableCell>
                  <TableCell>admin04</TableCell>
                  <TableCell>4</TableCell>
                  <TableCell> </TableCell>
                  <TableCell>
                    <Link to="/admin/account/modify"> </Link>
                  </TableCell>
                </TableRow>
              </TableBody>
            </MuiTable>
          </TableContainer>
        </div>
        <MuiPage />
      </ContentBox>
      <MuiModal
        open={open}
        onClose={handleClose}
        header={
          <>
            <h4> </h4>
            <Button
              icon="Delete"
              none
              onClick={handleClose}
              size="icon_s"
              iconStyle={{
                fill: "var(--c99)",
              }}
            >
              Close
            </Button>
          </>
        }
      >
        <div className="align top">
          <div className="lft" style={{ flex: 1 }}>
            <div style={{ width: "100%" }}>
              <h5 className="title3 mb_15">  </h5>
              <div className="mt_20">
                <label className="required body2">  </label>
                <Textarea
                  Height={90}
                  globalClass="mt_10"
                  counton
                  maxLength={150}
                />
              </div>
              <div className="mt_20">
                <label className="required body2"> </label>
                <FormGroup globalClass="mt_10">
                  <Radio
                    name="drink"
                    id="radio1"
                    value="drinkables"
                    defaultChecked
                  >
                     1
                  </Radio>
                  <Radio name="drink" id="radio2" value="Americano">
                     1
                  </Radio>
                </FormGroup>
              </div>
              <div className="mt_20">
                <label className="required body2"></label>
                <Input
                  globalClass="mt_10"
                  counton
                  maxLength={15}
                  onClick={() => {}}
                />
              </div>
              <div className="mt_20">
                <label className="required body2"></label>
                <Input
                  globalClass="mt_10"
                  counton
                  maxLength={15}
                  onClick={() => {}}
                />
              </div>
            </div>
          </div>
          <div className="rgt" style={{ width: 400, marginLeft: 15 }}>
            <div>
              <h5 className="title3 "> </h5>
              <p className="body2 mb_15 mt_10">
                     .
              </p>
              <div style={{}}></div>
            </div>
          </div>
        </div>
      </MuiModal>
    </Layout>
  );
}

export default ProductListPage;
